___CSS_LOADER_EXPORT___.push([module.id, `* {
    /* border: 1px solid red; */
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}



body {
    margin: 0;
}

#header {
    height: 10vh;
    margin: 0;
} 

#main {
    display: grid;
    grid-template-columns: 40vw 60vw;
}

#sidebar ul {
    list-style: none;
    padding: 0;
}

#sidebar {
    border: 1px solid gray;
    height: 90vh;
}

#sidebar * {
    margin-bottom: 5px;
}

#sidebar .project-title {
    font-size: 1.2em;
    font-weight: 1000;
    border-bottom: 2px dotted gray;
    /* margin-bottom: 120px;  */
}

#sidebar li ul {
    padding-top: 10px;
}

#sidebar li ul li div {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid lightgray;
    margin-right: 4px;
    margin-left: 4px;
}

#sidebar li ul li div button {
    background-color: pink;
    border-radius: 15px;
    font-weight: 1000;
}




.liItem {
    display: flex;
    flex-direction: row;
}

#mainWindow ul {
    padding: 0;
}

#mainWindow ul li div {
    width: 100%;
    display: grid; 
    grid-template-columns: 10vw 30vw 20vw;
    gap: 5px;
